<template>
	<div>
		<nav class="navbar navbar-transparent navbar-absolute">
			<div class="container">
				<div class="navbar-header">
					<button type="button" class="navbar-toggle"
							data-toggle="collapse"
							data-target="#navigation-example-2"
							@click="toggleNavbar">
						<span class="sr-only">Переключить навигацию</span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
					</button>
					<span class="navbar-brand">Reagent Store</span>
				</div>
				<div class="collapse navbar-collapse">
					<ul class="nav navbar-nav navbar-right">
						<router-link to="/login" tag="li"><a>Вход</a></router-link>
					</ul>
				</div>
			</div>
		</nav>
		<div class="wrapper wrapper-full-page">
			<div class="full-page login-page" data-color="blue" data-image="static/img/background/background-2.jpg">
				<!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
				<div class="content">
					<div class="container">
						<div class="row">
							<div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3">
								<form v-if="reseting" method="#" action="#">
									<div class="card" data-background="color" data-color="blue">
										<div class="card-header">
											<h3 class="card-title">Установите новый пароль</h3>
										</div>
										<div class="card-content">
											<div class="form-group">
												<label>Новый пароль</label>
												<input type="password" name="password" v-validate="modelValidations.password" v-model="model.password"
													placeholder="Пароль" class="form-control input-no-border" data-vv-as="Пароль">
												<small class="text-danger" v-show="password.invalid">{{ errors.first('password') }}</small>
											</div>
											<div class="form-group">
												<label>Повторите новый пароль</label>
												<input type="password" name="confirmPassword" v-validate="modelValidations.confirmPassword" v-model="model.verifyPassword"
													placeholder="Повторите пароль" class="form-control input-no-border" data-vv-as="подтверждения пароля">
												<small class="text-danger" v-show="confirmPassword.invalid">{{ errors.first('confirmPassword') }}</small>
											</div>
										</div>
										<div class="card-footer text-center">
											<button type="submit" class="btn btn-fill btn-wd" @click.prevent="validate" v-bind:disabled="!reseting">Продолжить</button>
											<div class="forgot m-t-10"><router-link to="/login">Уже есть аккаунт?</router-link></div>
										</div>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
				<footer class="footer footer-transparent">
					<div class="container"><div class="copyright">&copy; 2019, NSK</div></div>
				</footer>
				<div class="full-page-background" style="background-image: url(static/img/background/background-2.jpg) "></div>
			</div>
		</div>
		<div class="collapse navbar-collapse off-canvas-sidebar">
			<ul class="nav nav-mobile-menu">
				<router-link to="/login" tag="li"><a>Вход</a></router-link>
				<router-link to="/register" tag="li"><a>Регистрация</a></router-link>
			</ul>
		</div>
	</div>
</template>
<script>
	import { mapFields } from 'vee-validate'
	import swal from 'sweetalert2'
	export default {
		computed: {
			...mapFields([ 'password', 'confirmPassword' ])
		},
		created () {
			this.model.token = this.getToken()
			if (this.model.token !== '' && this.model.token.match(/[a-zA-Z0-9_-]+/)) {
				this.checkToken()
			} else {
				this.closeReseting()
				this.resetingError()
			}
		},
		data () {
			return {
				model: {
					token: '',
					password: '',
					verifyPassword: ''
				},
				modelValidations: {
					password: {
						required: true,
						min: 6
					},
					confirmPassword: {
						required: true,
						confirmed: 'password'
					}
				},
				reseting: false
			}
		},
		methods: {
			toggleNavbar () {
				document.body.classList.toggle('nav-open')
			},
			closeMenu () {
				document.body.classList.remove('nav-open')
				document.body.classList.remove('off-canvas-sidebar')
			},
			validate () {
				this.$validator.validateAll().then(isValid => {
					if (isValid) {
						this.resetPassword()
					}
				})
			},
			resetingError () {
				swal({
					title: 'Не верный токен',
					text: 'Ваша ссылка не действительна...',
					type: 'error'
				})
			},
			openReseting () {
				this.reseting = true
			},
			closeReseting () {
				this.reseting = false
			},
			getToken () {
				return this.$route.params.token
			},
			checkToken () {
				var self = this
				swal({
					title: 'Проверка токена',
					text: 'Пожалуйста, подождите...',
					onOpen: () => {
						swal.showLoading()
					},
					showConfirmButton: false,
					allowOutsideClick: false,
					allowEscapeKey: false,
					allowEnterKey: false
				})
				this.$store.dispatch('userCheckToken', {
					account: this.model,
					callback: function () {
						swal.close()
						self.openReseting()
					},
					successFalse: function () {
						self.resetingError()
					}
				})
			},
			resetPassword () {
				var self = this
				self.closeReseting()
				swal({
					title: 'Обновление пароля',
					text: 'Подождите, пожалуйста...',
					onOpen: () => {
						swal.showLoading()
					},
					showConfirmButton: false,
					allowOutsideClick: false,
					allowEscapeKey: false,
					allowEnterKey: false
				})
				this.$store.dispatch('userResetPassword', {
					account: this.model,
					callback: function (data) {
						swal({
							title: 'Успех',
							text: 'Пароль был обновлен',
							type: 'success',
							showConfirmButton: true
						}).then(function (result) { self.$router.push({ path: '/login' }) })
					},
					successFalse: function (data) {
						self.openReseting()
						swal({
							title: 'Упс, что то пошло не так',
							text: 'Попробуйте еще раз...',
							type: 'error'
						})
					}
				})
			},
			beforeDestroy () {
				this.closeMenu()
			}
		}
	}
</script>
<style>
</style>



